<template>
  <div id="customized-goodsLis">
    <CommonHeader :title="title"></CommonHeader>  
    <div class="container" v-for="(item,index) in goodList" :key="index" @click="toGoodsDetails(item)">
      <div class="goods-img">
        <img :src="item.imagePath" alt="" />
      </div>
      <div class="goods-info">
        <div class="goods-name">{{item.name}}</div>
        <div class="goods-content">
          <div class="goods-price">
            <em class="price-icon">￥</em>{{item.discountPrice}}</div>
          <div class="goods-comments">
            <!--<span class="goods-label">{{item.tags}}</span>-->
            <span class="goods-comment">{{item.commentCount}} 条评价</span>
            <span class="goods-rate fl-right">{{item.praiseCount}} 好评</span>
          </div>
        </div>
        <!--</router-link>-->
      </div>
    </div>
    <!-- <div class="container">
      <div class="goods-img">
        <img src="../../assets/GoodsList-pic.jpg" alt="" />
      </div>
      <div class="goods-info">
        <div class="goods-name">hhhhhhhhhh</div>
        <div class="goods-content">
          <div class="goods-price">
            <em class="price-icon">￥</em>123</div>
          <div class="goods-comments">
            <span class="goods-label">{{item.tags}}</span>
            <span class="goods-comment">123 条评价</span>
            <span class="goods-rate fl-right">123 好评</span>
          </div>
        </div>
       
      </div>
    </div>
    <div class="container">
      <div class="goods-img">
        <img src="../../assets/GoodsList-pic.jpg" alt="" />
      </div>
      <div class="goods-info">
        <div class="goods-name">123</div>
        <div class="goods-content">
          <div class="goods-price">
            <em class="price-icon">￥</em>123</div>
          <div class="goods-comments">
            <span class="goods-label">{{item.tags}}</span>
            <span class="goods-comment">123 条评价</span>
            <span class="goods-rate fl-right">123 好评</span>
          </div>
        </div>
  
      </div>
    </div> -->
    <div class="shopping-box" @click="linkTo('CustomizedBox')">
      <img src="../../assets/home_btn_basket@3x.png" alt="">
    </div>
  </div>
</template>

<script>
//   import Bus from '@/bus.js'
import CommonHeader from "@/components/Header/CommonHeader";

export default {
  //     props: ['goodList'],
  data() {
    return {
      title:this.$route.query.title,
      goodList: [],
      PARAMS: {
        a: 0,
        type: 5,
        pa: 1,
        li: 10,
        ob: 7,
        orgId: this.$route.query.orgId,
        spuId: "",
        promId: "",
        categoryId: "",
        searchKey: "",
        lastId: ""
      }
    };
  },
  created() {
    this.paramsInit();
  },
  methods: {
    toGoodsDetails(item){
       this.$router.push({
        name: "CustomizedGoodsDeatail",
        query: {
          promId: item.promId,
          skuId: item.skuId,
          spuId:item.spuId,
        }
      });
    },
    paramsInit() {
      //				console.log(this.$route.query.orgId)
      this.PARAMS = {
        a: this.$route.query.a ? this.$route.query.a : "0",
        type: this.$route.query.type ? this.$route.query.type : 5,
        pa: 1,
        li: 10,
        ob: 7,
        orgId: this.$route.query.orgId ? this.$route.query.orgId : "0",
        spuId: this.$route.query.spuId ? this.$route.query.spuId : "",
        promId: this.$route.query.promId ? this.$route.query.promId : "",
        categoryId: this.$route.query.categoryId
          ? this.$route.query.categoryId
          : "",
        searchKey: this.$route.query.searchKey
          ? this.$route.query.searchKey
          : "",
        lastId: ""
      };
      //				console.log(this.PARAMS)
      this.getGoodsList();
    },
    getGoodsList() {
      Object.keys(this.PARAMS).forEach(
        key => this.PARAMS[key] == "" && delete this.PARAMS[key]
      );
      this.$Api.getGoodsList(this.PARAMS).then(res => {
        console.log(res);
        if (res.q.s == 0) {
          if (this.PARAMS.pa == 1) {
            this.goodList = res.q.goodses;
          } else if (this.PARAMS.pa > 1 && res.q.goodses.length > 0) {
            let gl = this.goodList;
            this.goodList = gl.concat(res.q.goodses);
          }
        }
      });
    }
    // toGoodsDetails(item) {
    //   console.log(item)
    //   let promId = item.promId
    //   let skuId = item.skuId
    //   let spuId = item.spuId
    //   let type = item.type
    //   this.$router.push({
    //     name: 'GoodDetail',
    //     query: {
    //       promId,
    //       skuId,
    //       spuId,
    //       type
    //     }
    //   })
    // },
  },
  components: {
    CommonHeader
  }
};
</script>

<style lang="scss">
@import "../../sass/baseSass.scss";
#customized-goodsLis {
  .shopping-box {
    width: box(90);
    height: box(90);
    position: fixed;
    bottom: box(60);
    right: box(30);
    z-index: 999;
  }
  .container {
    display: flex;
    height: 3.47rem;
    padding: 0.27rem 0.4rem;
    border-bottom: 1px solid $border-color;
    .goods-img {
      width: 2.93rem;
      height: 2.93rem;
      border-radius: 8px;
      > img {
        width: 2.93rem;
        height: 2.93rem;
        border-radius: 8px;
      }
    }
    .goods-info {
      position: relative;
      flex: 1;
      line-height: 1;
      margin-left: 0.35rem;
      text-align: left;
      .goods-name {
        font-size: 0.37rem;
        line-height: 1.286;
        @include textflow-two;
        color: $text-color;
      }
      .goods-content {
        position: absolute;
        width: 100%;
        bottom: 0.07rem;
      }
      .goods-price {
        font-size: 0.45rem;
        font-weight: bold;
        padding-top: 0.93rem;
        color: $price-color;
        .price-icon {
          font-size: 0.37rem;
        }
      }
      .goods-comments {
        font-size: 0.32rem;
        margin-top: 0.24rem;
        color: $text-color2;
        .goods-label {
          @include goodslabel;
        }
      }
    }
  }
}
</style>



